.kl-card {
  border-radius: 20px;
  box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
  border: 2px solid var(--dark-grey-1, #1c1c1c);

  h3 {
    text-decoration: none;
  }

  .hs-form-field {
    margin-bottom: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    text-decoration: none;
    margin-bottom: 13px;
  }

  p {
    margin-bottom: 22px;
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
  }
}

@mixin input {
  color: var(--dark-grey-1, #1c1c1c);
  font-size: 18px;
  font-style: normal;
  line-height: normal;
}

.simple-card {
  background: var(--light-blue-1, #a3deff);
}

.newsletter-card {
  background: var(--green-1, #73bfa3);
  display: flex;
  gap: 30px;
  padding: 30px 25px;

  .image-wrapper {
    flex-shrink: 0;
  }
  .content-wrapper {
    flex: 1;
  }

  .kl-card__image {
    margin-bottom: 0;
  }

  img {
    width: 194px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
  }

  input[type="email"] {
    @include input;
    border-radius: 52px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    background: var(--light-grey-1, #f2f2f0);
    height: 60px;
    margin-bottom: 13px;
    padding: 20px 35px;

    &::placeholder {
      @include input;
    }
  }

  input[type="submit"] {
    border-radius: 52px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    background: var(--dark-grey-1, #1c1c1c);

    color: var(--light-grey-1, #f2f2f0);
    text-align: center;
    font-size: 18px;
    font-style: normal;
    font-weight: 800;
    line-height: normal;

    &:hover {
      background-color: var(--light-grey-1, #f2f2f0);
      border-color: var(--light-grey-1, #f2f2f0);
      color: var(--dark-grey-1, #1c1c1c);
      text-decoration: none;
      transform: none;
    }
  }
}

.kl-testimonials {
  .kl-testimonial__quote svg {
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 80px;
    font-style: normal;
    font-weight: 800;
    line-height: 24px; /* 30% */
    opacity: 0.1;
  }
}
